body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  
}

.middle {
  background: linear-gradient(#e66465, #9198e5);
  border: 3px solid black;
  width: 100%;
  height: 50rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.navbar {
  margin: 0 50px;
}

.nav-li {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-bottom: 0;
}

.mgl20 {
  margin-left: 20px;
  padding: 1.1rem 0 .8rem 0;
}
/*NAV BAR*/
li {
  position: relative;
  cursor: pointer;
  color: gray;
  font-weight: 500;
}
.test ul li::after {
  content: '';
  position: relative;
  display: block;
  margin: auto;
  height: 3px;
  width: 0px;
  background: transparent;
  top: 3px;
  transition: width .75s ease, background-color .75s ease;
}
.test ul li:hover::after {
  width: 100% ;background: #c1E73E;
}


